@import './common.scss';

.pets {
    background: $light_grey_bg;
    padding: 80px 0 100px;
    height: 877px;
    box-sizing: border-box;
    
    
    .pets_header {
        font-size: 35px;
        font-weight: normal;
        line-height: 130%;
        text-align: center;
        letter-spacing: 0.06em;
        color: $main_grey;
        margin-bottom: 59px;
    }
    .pets_slider {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 60px;

        
        .pet_slider_arrow {
            min-width: 52px;
            width: 52px;
            height: 52px;
            background: transparent;
            margin: auto 0;
         
            &:hover {
                background: $primary_hover;
            }
            
            &.to_left {
                transform: scale(-1, 1);
                margin-right: 12px;
            }
            &.to_right {
                margin-left: 12px;
            }
        }
        
    }
   .pets .pets_slider_buttons_mobile {
        display: none;
    }
    
    .pets_cards  {
        display: flex;
        justify-content: space-between;
        width: 100%;
        max-width: 990px;
        
        .pet_card {
            background: $non_active_card_bg;
            width: 270px;
            height: 435px;
            padding-bottom: 30px;
            margin-right: 90px;
            box-sizing: border-box;
            border-radius: 9px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            
            &:hover, &.--active {
                box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
                background: $white;
                cursor: pointer;
            }
            
            &:hover .pet_card_btn  {
                background: $primary_hover;
            }
            
            &:last-child {
                margin-right: 0;
            }
     
            
            .pet_photo {
                width: 270px;
                height: 270px;
                margin-bottom: 29px;
            }
            .pet_name {
                font-family: 'Georgia';
                font-size: 20px;
                line-height: 23px;
                letter-spacing: 0.06em;
                color: $main_grey;
                margin-bottom: 30px;
                text-align: center;
            }
            .pet_card_btn {
                width: 187px;
                height: 52px;
                background: transparent;
                margin: 0 auto;
                
                
            }
        }
        
        
    }
    .pet_slider_rest {
            width: 261px;
            margin: 0 auto;
        }
    
    
}