footer {
  background: url("../images/noise_transparent2x.png"), radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%), #211F20;
  padding-top: 37px;
  height: 350px;
  box-sizing: border-box;
  overflow: hidden;
}
footer .footer_wrapper {
  display: flex;
  justify-content: space-between;
}
footer .footer_contacts {
  max-width: 300px;
}
footer .footer_location {
  max-width: 322px;
}
footer .footer_questions, footer .footer_visit {
  display: flex;
  flex-direction: column;
  font-weight: normal;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 0.06em;
  color: #ffffff;
  margin-top: 18px;
}
footer .footer_email {
  margin: 38px 0 40px;
}
footer .footer_address:first-of-type {
  margin: 32px 0 35px;
}
footer .footer_email,
footer .footer_phone,
footer .footer_address {
  font-weight: normal;
  font-size: 20px;
  line-height: 110%;
  letter-spacing: 0.06em;
  color: #F1CDB3;
  text-decoration: none;
  display: flex;
  align-items: center;
}
footer .footer_email img,
footer .footer_phone img,
footer .footer_address img {
  margin-right: 20px;
}
footer .footer_photo_puppy {
  width: 300px;
  height: 315px;
}

@media (max-width: 1279px) {
  footer {
    height: auto;
    padding-top: 30px;
  }

  footer .footer_wrapper {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  footer .footer_contacts, footer .footer_location {
    margin-right: 0;
    max-width: 45%;
    box-sizing: border-box;
  }

  footer .footer_contacts {
    padding-left: 16px;
  }

  footer .footer_questions, footer .footer_visit {
    margin-top: 0;
  }

  footer .footer_photo_puppy {
    margin: 65px auto 0;
  }
}
@media (max-width: 767px) {
  footer .footer_wrapper {
    flex-direction: column;
    align-items: center;
  }

  footer .footer_questions, footer .footer_visit {
    text-align: center;
    font-size: 25px;
    line-height: 130%;
    letter-spacing: 0.06em;
  }

  footer .footer_contacts,
footer .footer_location {
    max-width: 100%;
    padding: 0;
  }

  footer .footer_photo_puppy {
    margin-top: 0;
    width: 260px;
    height: 269px;
  }

  footer .footer_photo_puppy img {
    width: 260px;
    height: 269px;
  }

  footer .footer_email, footer .footer_phone {
    margin: 40px auto;
    justify-content: center;
  }

  footer .footer_visit {
    max-width: 95%;
    margin: 0 auto;
  }

  footer .footer_address, footer .footer_address:first-of-type {
    margin: 40px auto;
    width: 300px;
  }

  footer .footer_contacts {
    margin-right: 0;
  }
}
