header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 60px 0;
}
header .logo .logo_title {
  font-family: "Georgia";
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 110%;
  letter-spacing: 0.06em;
  color: #F1CDB3;
  margin-bottom: 10px;
  display: block;
}
header .logo .logo_subtitle {
  font-family: "Arial";
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.1em;
  color: #ffffff;
  text-align: center;
}
header .burger {
  display: none;
}
header .nav {
  display: flex;
  padding-bottom: 2px;
  list-style: none;
}
header .nav .nav_item {
  margin-left: 35px;
  margin-right: 1px;
}
header .nav .nav_link {
  font-family: "Arial";
  font-size: 15px;
  line-height: 160%;
  color: #CDCDCD;
  text-decoration: none;
  display: block;
}
header .nav .nav_link:hover {
  color: #FAFAFA;
}
header .nav .nav_link.nav_link--active {
  color: #FAFAFA;
  border-bottom: 3px solid #F1CDB3;
}
header .nav-mobile-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  transition: right 0.3s ease-in-out;
  right: -100%;
}
header .nav-mobile-wrapper .nav-mobile {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  right: 0;
  padding: 0 38px;
  width: 320px;
  height: 100%;
  background: #292929;
  box-sizing: border-box;
}
header .nav-mobile-wrapper .nav-mobile .nav_item-mobile {
  font-size: 32px;
  line-height: 160%;
  margin-bottom: 40px;
  text-align: center;
}
header .nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link {
  color: #CDCDCD;
  text-decoration: none;
}
header .nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link.nav_link--active {
  color: #FAFAFA;
  border-bottom: 3px solid #F1CDB3;
}
header .nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link.nav_link--inactive:hover {
  color: #CDCDCD;
  cursor: not-allowed;
}
header .nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link:hover {
  color: #FAFAFA;
}
header .nav-mobile-wrapper.--active {
  right: 0;
  transition: right 0.3s ease-in-out;
}
header .nav-mobile-wrapper::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  visibility: hidden;
  opacity: 0;
}
header .nav-mobile-wrapper.--active::before {
  opacity: 1;
  visibility: visible;
}

body.--blocked {
  overflow: hidden;
  background: rgba(41, 41, 41, 0.6);
}

@media (max-width: 767px) {
  header .nav {
    display: none;
  }

  header .burger {
    display: flex;
    align-items: center;
    margin-right: 43px;
    z-index: 1;
    transition: transform 0.3s ease-in-out;
  }
  header .burger.--active {
    transform: rotate(90deg);
    transition: transform 0.3s ease-in-out;
  }

  header .burger:hover {
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  .nav-mobile-wrapper {
    display: none;
  }
}
