/* popup */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(41, 41, 41, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.close-btn {
  position: absolute;
  width: 52px;
  height: 52px;
  border: 2px solid #F1CDB3;
  box-sizing: border-box;
  border-radius: 100px;
  background-image: url("../images/icons/close-popup.svg");
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
  right: -40px;
  top: -50px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.close-btn:hover {
  background-color: #FDDCC4;
  transition: all 0.3s ease-in-out;
}

.popup-wrapper {
  display: flex;
  width: 900px;
  min-height: 500px;
  height: auto;
  box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
  background: #FAFAFA;
  border-radius: 9px;
  position: relative;
}

.popup-img {
  width: 500px;
  height: 500px;
  flex-shrink: 0;
}
.popup-img img {
  width: 100%;
  border-radius: 9px 0px 0px 9px;
}

.popup-text {
  padding: 50px 20px 50px 30px;
}

.popup-name {
  font-weight: 400;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 0.06em;
  margin-bottom: 10px;
}

.popup-type {
  font-weight: 400;
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
  margin-bottom: 40px;
}

.popup-description {
  font-weight: 400;
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
  margin-bottom: 40px;
}

.popup-list {
  font-size: 15px;
  line-height: 110%;
  letter-spacing: 0.06em;
  font-weight: 400;
}
.popup-list li {
  margin-bottom: 10px;
  position: relative;
  padding-left: 14px;
}
.popup-list li::before {
  position: absolute;
  left: 0;
  top: 7px;
  content: "";
  width: 4px;
  height: 4px;
  background: #F1CDB3;
  border-radius: 50%;
}

