@import './common.scss';
@import './footer.scss';
@import './header.scss';
@import './pet-cards.scss';


.start_screen {
    background: url('../images/noise_transparent2x.png'), radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%), #211F20;
    width: 100%;
    height: 908px;
    
    .start_screen_puppy {
        width: 698px;
        height: 728px;
        padding-right: 1px;
    }
}




.start_screen_wrapper {
    display: flex;
    justify-content: space-between;
    
    .start_screen_text {
        margin-top: 163px;
    }
    
    .start_screen_header {
        font-size: 44px;
        font-weight: normal;
        line-height: 130%;
        color: $white;
        margin-bottom: 41px;
        
    }
    .start_screen_description {
        font-family: 'Arial';
        font-size: 15px;
        line-height: 160%;
        color: $light_grey_main-screen;
        margin-bottom: 41px;
        max-width: 470px;
    }
    .start_screen .start_screen_puppy {
        display: flex;
    }
    
}
.about_shelter {
    height: 588px;
    box-sizing: border-box;
}
.about_shelter_wrapper {
    padding: 80px 176px 100px 174px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    .about_shelter_text {
        width: 430px;
    }
    
    .about_shelter_header {
        font-size: 35px;
        font-weight: normal;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: $main_grey;
        margin-bottom: 24px;
    }
    .about_shelter_description {
        font-family: 'Arial';
        font-size: 15px;
        line-height: 160%;
        color: $text-about;
        margin-bottom: 25px;
    }
    
    .about_shelter_description:last-child {
        margin-bottom: 4px;
    }
    
}


.help {
    padding: 80px 0 100px;
    box-sizing: border-box;


    
    .help_header {
        font-weight: normal;
        font-size: 35px;
        line-height: 130%;
        letter-spacing: 0.06em;
        color: $main_grey;
        text-align: center;
        margin-bottom: 60px;
    }
    
    .help_items_wrapper {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
      
        
        .help_item {
            margin-right: 120px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            
            .help_icon {
                height: 60px;
                width: auto;
                margin-bottom: 29px;
            }
            
            .help_title {
                font-weight: normal;
                font-size: 20px;
                line-height: 115%;
                letter-spacing: 0.06em;
                color: $main_grey;
            }
            
        }
        .help_item:nth-child(5), 
        .help_item:nth-child(9) {
            margin-right: 0px;
        }
        .help_item:nth-child(1),
        .help_item:nth-child(2),
        .help_item:nth-child(3),
        .help_item:nth-child(4),
        .help_item:nth-child(5) {
            margin-bottom: 56px;
        }
        
    }
    
}
.donation {
    background: $light_grey_bg;
    padding: 81px 0 100px;
    
    .donation_wrapper {
        display: flex;
        align-items: center;
        padding: 0 143px 0 141px;
        justify-content: space-between;
        
        .donation_photo_dog {
            width: 505px;
            height: 261px;
            margin-right: 30px;
            padding-top: 1px;
        }
        
        
        .donation_header  {
            font-weight: normal;
            font-size: 35px;
            line-height: 130%;
            letter-spacing: 0.06em;
            margin-bottom: 19px;
            color: $main_grey;
        }
        
        .donation_bank_name {
            font-weight: normal;
            font-size: 15px;
            line-height: 110%;
            letter-spacing: 0.06em;
            color: $main_grey;
            margin-bottom: 20px;
            
        }
        
        .donation_credit_card {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 349px;
            height: 43px;
            border-radius: 9px;
            background: $primary;
            padding: 10px 14px 10px 15px;
            box-sizing: border-box;
            margin-bottom: 21px;
            &:hover{
      
                background: #FDDCC4;
            }
            
            .donation_credit_card_numbers {
                font-weight: normal;
                font-size: 20px;
                line-height: 115%;
                letter-spacing: 0.06em;
                color: $main_grey;
                text-decoration: none;
                    &:hover{
                    text-decoration: underline;
      
                }
            }
            
        }
        .donation_description {
            font-family: 'Arial';
            font-style: italic;
            font-weight: normal;
            font-size: 12px;
            line-height: 18px;
            color: $light_grey_italic;
        }
        
        
        
    }
    
}

@media (max-width: 1279px){
    .center {
        padding: 0 30px;
    }
    header {
        padding: 30px 0;
    }
    .start_screen {
        height: auto;
    }
    .start_screen_wrapper {
        flex-direction: column;
        align-items: center;
    }
    
    .start_screen_wrapper .start_screen_text {
        margin-top: 30px;
        width: 460px;
    }
    .start_screen_btn {
        margin: auto;
    }
    .start_screen .start_screen_puppy{
        margin-left: auto;
        margin-top: 102px;
        width: 569px;
        height: 593px;
        display: flex;
    }
    .start_screen_puppy img {
        width: 569px;
        height: 593px;
    }
    
    .about_shelter {
        height: auto;
    }
    
    .about_shelter_wrapper {
        flex-direction: column;
        padding: 80px 0 95px;
    }
    
    .about_shelter_wrapper .about_shelter_text {
        order: -1;
        margin-bottom: 77px;
    }
    .pets .pets_slider {
        max-width: 708px;
        margin: 0 auto 60px;
    }
    
    .pets .pets_cards .pet_card:nth-child(3) {
        display: none;
    }
    .pets .pets_cards {
        justify-content: space-between;
    }
    .pets .pets_cards .pet_card {
        margin: 0;
    }
    .help {
        padding-bottom: 43px;
      }
    .help .help_items_wrapper .help_item {
        margin: 0 30px 56px;
        width: 170px;
    }
    .help .help_items_wrapper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 1fr);
        max-width: 630px;
        margin: 0 auto;
    }
    
    .donation .donation_wrapper {
        flex-direction: column;
    }
    .donation_text {
        order: -1;
        margin: 0 auto 58px;
        width: 380px;
    }
    .donation .donation_wrapper {
        padding: 0;
    }
   
    .donation .donation_wrapper .donation_photo_dog{
        margin: 0 auto;
    }
    
}


@media (max-width: 767px) {
    .pets .pets_cards .pet_card:nth-child(2),   .pets .pets_cards .pet_card:nth-child(3){
        display: none;
    }
    .center {
        padding: 0 10px;
    }
    
    .start_screen_wrapper .start_screen_header {
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        text-align: center;
    }
    .start_screen_wrapper .start_screen_description {
        text-align: center;
        margin: 0 auto 42px;
    }
    .start_screen_wrapper .start_screen_text {
        margin-top: 10%;
        width: 100%;
    }
    .start_screen .start_screen_puppy {
        width: 85%;
        min-width: 260px;
        height: auto;
        margin-top: 107px;
    }
    .start_screen_puppy img {
        width: 100%;
        height: auto;
    }
    
    .about_shelter_wrapper {
        padding: 42px 15px;
    }
    .about_shelter_wrapper .about_shelter_text {
        width: 100%;
        margin-bottom: 42px;
        text-align: justify;
    }
    
    .about_shelter_wrapper .about_shelter_header {
        text-align: center;
        font-size: 25px;
line-height: 130%;
    }
    .about_shelter_wrapper .about_shelter_description {
        margin-bottom: 20px;
    }
    .about_shelter_wrapper .about_photo_pets {
        width: 260px;
        height: 353px;
    }
    .about_shelter_wrapper .about_photo_pets img {
        width: 100%;
        height: auto;
    }
    
    .pets {
        padding: 40px 0;
        height: auto;
    }
    .pets .pets_header {
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        margin-bottom: 42px;
    }


    .pets .pets_cards {
        width: 100%;
        grid-area: card;
    }
    .pets .pets_cards .pet_card {
        margin: 0 auto;
    }
    
    
    .pets .pets_slider_buttons_mobile, .pet_slider_arrow.btn-mobile {
        display: flex;
    }
     .pets .pets_slider {
         max-width: 270px;
         margin: 0 auto 40px;
        display: grid;
        grid-template-columns: 135px 135px; 
        grid-template-rows: auto auto; 
        gap: 0px 0px; 
        grid-row-gap: 20px;
        justify-items: center; 
        grid-template-areas: 
            "card card"
            "left right"; 
    }
    
  
    
    .pets .pets_slider .pet_slider_arrow.to_left {
        grid-area: left;
        margin: 0;
    }
    
    .pets .pets_slider .pet_slider_arrow.to_right {
        grid-area: right;
        margin: 0;
    }
    .pets .pets_slider_buttons_mobile {
 
        justify-content: center;
        margin: 20px auto 42px;
    }
   
    .pets .pets_slider .pet_slider_arrow.btn-mobile {
        margin: 0 40px;
    }
    
    .help {
        padding: 42px 0;
    }
    .help .help_header {
        font-size: 25px;
        line-height: 130%;
        letter-spacing: 0.06em;
        text-align: center;
        margin-bottom: 42px;
    }
    
    .help .help_items_wrapper{
        justify-content: space-between;
        padding: 0 calc((100% - 290px)/2);
        display: flex;
    }
    
    .help .help_items_wrapper .help_item {
        width: 130px;
        height: 87px;
        margin: 0 0 30px 0;
        text-align: center;
    }
    .help .help_items_wrapper .help_item:last-child{
        margin-right: auto;
        margin-bottom: 0;
    }
    .help .help_items_wrapper .help_item:nth-child(1), .help .help_items_wrapper .help_item:nth-child(2), .help .help_items_wrapper .help_item:nth-child(3), .help .help_items_wrapper .help_item:nth-child(4), .help .help_items_wrapper .help_item:nth-child(5){
        margin-bottom: 30px;
    }
    .help .help_items_wrapper .help_item:nth-child(5), .help .help_items_wrapper .help_item:nth-child(9) {
        margin-right: 0;
    }
    .help .help_items_wrapper .help_item:nth-child(2n){
        margin-right: 0;
    }
    .help .help_items_wrapper .help_item .help_title {
        font-size: 15px;
line-height: 110%;

    }
    
    .help .help_items_wrapper .help_item .help_icon {
        margin-bottom: 20px;
        width: 50px;
        height: 50px;
    }
    .help .help_items_wrapper .help_item .help_icon img {
        width: 100%;
        height: auto;
    }
    
    .donation {
        padding: 42px 10px;
    }
    
    .donation .donation_wrapper .donation_header{
        font-size: 25px;
line-height: 130%;
letter-spacing: 0.06em;
text-align: center;
    }
    .donation_text {
        width: 100%;
        margin-bottom: 42px;
    }
    .donation .donation_wrapper .donation_bank_name{
        font-size: 13px;
line-height: 160%;
text-align: center;
    }
    .donation .donation_wrapper .donation_credit_card{
        width: 281px;
        margin: 21px auto 20px;
    }
    
    .donation .donation_wrapper .donation_credit_card .donation_credit_card_numbers{
        font-size: 15px;
line-height: 110%;
    }
    .donation .donation_wrapper .donation_description {
        text-align: justify;
        
    }
    .donation .donation_wrapper .donation_header {
        margin-bottom: 22px;
    }
    
    .donation .donation_wrapper .donation_photo_dog, .donation .donation_wrapper .donation_photo_dog img {
        width: 260px;
        height: 135px;
    }
    
    
    
}

